}  
.blinking {
  animation: blinkingText 0.82s infinite;
}
.blink{
  left: 0;
  position: "absolute";
}
.previousButton{
left: 0 !important;
}
.slider a.previousButton, .slider a.nextButton{
color: rgb(202, 63, 63) !important;
}
.nextButton{
right:0 !important;
}
@keyframes blinkingText {
  0% {
    color: rgb(250, 6, 6);
  }
  49% {
    color: rgb(255, 0, 0);
  }
  60% {
    color: transparent;
  }
  99% {
    color: transparent;
  }
  100% {
    color: rgb(255, 0, 0);
  }
}
